import React, { Component } from 'react';
import * as d3 from 'd3';
import { Table, Column, AutoSizer } from 'react-virtualized';
import ReactPaginate from "react-paginate";
import BoxPlotTable from "../chart/boxPlot";
import ChartComponent from "../chart/chartComponent";
import 'react-virtualized/styles.css';
import './table.css';

const generateRandomId = () => {
    let text = "";
    let possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    for (let i = 0; i < 30; i++)
        text += possible.charAt(Math.floor(Math.random() * possible.length));

    return text;
};
class VirtualizedTable extends Component {
    constructor (props) {
        super(props);
        this.state = {
            isShow: false,
            miniChart: false, isChartView: false,
            allColumns:null, allLists:null,
            tableId : generateRandomId(),
            tbFirstColumn:0,
            displayColumns:3,
            listData:[], list:[],
            columnData:[], columns:[],
            tbFirstHeight:100,
            rowHeight:40, headerHeight:50,
            width:650, height:650,
            pageItem:10, selectedPage:1,
            barData:[], histData:[],
            headerSelectedKey:null,
            newSelectedLabel:null,
            deletedColumn:[],
            bookmarked:[]
        };
    }

    componentWillReceiveProps (nextProps) {
        if(nextProps.isShow !== this.state.isShow){
            this.setState({isShow:nextProps.isShow}, ()=>{this.initilizeTableDimsFirst();})
        }
        if(nextProps.bookmarked !== this.state.bookmarked){
            this.setState({bookmarked:nextProps.bookmarked})
        }

        if(nextProps.deletedColumn !== this.state.deletedColumn){
            this.setState({deletedColumn:nextProps.deletedColumn})
        }

        if(nextProps.width > 100 && nextProps.width !== this.state.width){
            this.setState({width:(nextProps.width > 650 ? nextProps.width : 650)},()=>{
                this.setStateListAndColumnData();
            });
        }

        if(!!nextProps.data){
            if(nextProps.data.bar !== this.state.barData){
                this.setState({barData:nextProps.data.bar})
            }

            if(nextProps.data.hist !== this.state.histData){
                this.setState({histData:nextProps.data.hist})
            }

            if(nextProps.data.preview1 !== this.state.allLists || nextProps.data.columns !== this.state.allColumns){
                this.setState({allLists:nextProps.data.preview1, allColumns:nextProps.data.columns, columnData:[], listData:[], list:[], columns:[]},()=>{
                    this._setStateListAndColumns();
                })
            }
        }

        if(nextProps.selectedLabels !== this.state.newSelectedLabel){
            this.setState({newSelectedLabel:nextProps.selectedLabels});
        }
    }

    componentDidMount () {
        this.initilizeTableDimsFirst();
    }

    _setStateListAndColumns(){
        const {allLists, allColumns} = this.state;
        let columns = []; let list = [];
        Object.keys(allLists).forEach((v) => {
            list.push(allLists[v]);
        });
        Object.keys(allColumns).forEach((value, index) => {
            columns.push({ index:index, type: allColumns[value], label: value, dataKey: value  })
        });
        this.setState({columnData:columns, listData:list, tbFirstColumn:0}, ()=>{
            this.setStateListAndColumnData();
        });
    }

    setStateListAndColumnData(){
        let list = []; let miniChart = false;
        const { columnData, listData, barData, histData, selectedPage, pageItem } =this.state;
        if(columnData.length > 0){
            listData.forEach((v, i)=>{
                if(((i + 1 ) > ((selectedPage - 1) * pageItem)) && ((i + 1 ) <= (selectedPage * pageItem))){
                    if((i === ((selectedPage - 1) * pageItem)) && barData.length > 0 && histData.length > 0){
                        miniChart = true; list.push({...v, chart:true});
                    }
                    list.push(v);
                }
            });
            let columns = columnData.map((value, i)=>{
                let colObj = value;
                colObj.index = i;
                colObj.width = this.calculateColumnWidth();
                colObj.headerRenderer = (parms) => {
                    const {label} = parms;
                    return (<span className="ReactVirtualized__Table__headerTruncatedText">{label}</span>);
                };
                colObj.cellRenderer = (params) => {
                    const {rowData, dataKey} = params;
                    if(rowData.chart){
                        let chartType = value.type === 'N' ? 'histo' : 'bar';
                        let chartData = value.type === 'N' ? histData.find(v => v.cols === value.dataKey) : barData.find(v => v.cols === value.dataKey);
                        return (
                            !!chartData && !!chartData.data && chartData.data.length > 0 ?
                                <div style={{'cursor': 'pointer'}} onClick={this.onMiniChartClick.bind(this, rowData, dataKey)}
                                     onMouseOver={this.onColumnMouseOver.bind(this, rowData, dataKey)}
                                     onMouseOut={this.onColumnMouseOut.bind(this)} >
                                    <ChartComponent
                                        mode="micro"
                                        chart_type={chartType}
                                        width={this.calculateColumnWidth()}
                                        height="100"
                                        chartData={chartData.data}
                                    />
                                </div> : <div style={{'cursor': 'pointer'}} onMouseOver={this.onColumnMouseOver.bind(this, rowData, dataKey)}
                                              onMouseOut={this.onColumnMouseOut.bind(this)} > --- </div>)
                    }else{
                        return rowData[dataKey];
                    }
                };
                return colObj;
            });
            this.setState({list, columns, miniChart}, ()=>{this.initilizeTableDimsFirst()});
        }
    }

    _getDisplayColumns(){
        const { displayColumns, columnData, width } = this.state;
        let colDisplay = displayColumns;
        if(!!this.props.displayColumns && columnData.length > 0){
            colDisplay = parseInt(width / 150);
            // colDisplay = this.props.displayColumns;
        }
        return (columnData.length - colDisplay) > 0 ? colDisplay : columnData.length;
    }

    onClickScroll(type){
        const { tbFirstColumn, columns, tableId } = this.state;
        const scColDis = columns.length - this._getDisplayColumns();
        const colWidth = this.calculateColumnWidth();
        d3.select(".virtualized-table-" +tableId+ " .ReactVirtualized__Grid__innerScrollContainer").selectAll(".table-header-toolTip").remove();
        const headerRow = document.querySelector(`.virtualized-table-${tableId} .ReactVirtualized__Table__headerRow`);
        const tableGrid = document.querySelector(`.virtualized-table-${tableId} .virtulized-scroll`);
        if(type === 'left' && tbFirstColumn < scColDis){
            headerRow.scrollLeft = (tbFirstColumn * colWidth) + colWidth;
            tableGrid.scrollLeft = (tbFirstColumn * colWidth) + colWidth;
            this.setState({tbFirstColumn : tbFirstColumn + 1}, ()=>{ this.initilizeTableDimsFirst()});
        }else if(type === 'right' && tbFirstColumn > 0){
            headerRow.scrollLeft = (tbFirstColumn * colWidth) - colWidth;
            tableGrid.scrollLeft = (tbFirstColumn * colWidth) - colWidth;
            this.setState({tbFirstColumn : tbFirstColumn - 1}, ()=>{ this.initilizeTableDimsFirst()});
        }
    }

    onMiniChartClick = (rowData, dataKey, e) =>{
        const { tableId } = this.state;
        this.setState({isChartView:true, headerSelectedKey:dataKey},()=>{
            d3.select(".virtualized-table-" +tableId+ " .ReactVirtualized__Grid__innerScrollContainer").selectAll(".table-toolTip").remove();
            document.querySelector(`.virtualized-table-${tableId} .ReactVirtualized__Table__Grid`).style.display = "none";
        });
    }

    backToTable = (e) => {
        const { tableId } = this.state;
        this.setState({isChartView:false, headerSelectedKey:null},()=>{
            d3.select(".virtualized-table-" +tableId+ " .ReactVirtualized__Grid__innerScrollContainer").selectAll(".table-toolTip").remove();
            const headActClass = document.querySelector(`.virtualized-table-${tableId} .ReactVirtualized__Table__headerRow .header-active`);
            !!headActClass && headActClass.classList.remove('header-active');
            document.querySelector(`.virtualized-table-${tableId} .ReactVirtualized__Table__Grid`).style.display = "inline-block";
        });
    }

    onColumnMouseOver = (rowData, dataKey, e) =>{
        const {columns, tableId} = this.state;
        const colWi = this.calculateColumnWidth();
        let toolTipData = null;
        let ind = columns.find((v)=>v.dataKey === dataKey).index;
        !!this.props.data && !!this.props.data.summary_stats && Object.keys(this.props.data.summary_stats).forEach((v) => {
            if(this.props.data.summary_stats[v]['Column Name'] === columns[ind].dataKey){
                toolTipData = this.props.data.summary_stats[v];
            }
        });
        d3.select(".virtualized-table-" +tableId+ " .ReactVirtualized__Grid__innerScrollContainer").selectAll(".table-toolTip").remove();
        if(!!toolTipData){
            let tooltipDiv = d3.select(".virtualized-table-" +tableId+ " .ReactVirtualized__Grid__innerScrollContainer").append("div").attr("class", "table-toolTip");
            tooltipDiv.style("left", (colWi * ind)+"px");
            tooltipDiv.style("top", "100px");
            tooltipDiv.style("display", "inline-block");
            tooltipDiv.html("<table style='width:"+(colWi - 10)+"px;'>" +
                "<tr style='width:"+(colWi)+"px; height: 40px;'><td style='width: 50%;text-align: start'>Mean</td><td style='width: 50%;text-align: end'>"+(toolTipData.Mean ? toolTipData.Mean : '-')+"</td></tr>" +
                "<tr style='width:"+(colWi)+"px; height: 40px;'><td style='width: 50%;text-align: start'>Type</td><td style='width: 50%;text-align: end'>"+(toolTipData.Type ? toolTipData.Type : '-')+"</td></tr>" +
                "<tr style='width:"+(colWi)+"px; height: 40px;'><td style='width: 50%;text-align: start'>Min</td><td style='width: 50%;text-align: end'>"+(toolTipData.Min ? toolTipData.Min : '-')+"</td></tr>" +
                "<tr style='width:"+(colWi)+"px; height: 40px;'><td style='width: 50%;text-align: start'>Max</td><td style='width: 50%;text-align: end'>"+(toolTipData.Max ? toolTipData.Max : '-')+"</td></tr>" +
                "<tr style='width:"+(colWi)+"px; height: 40px;'><td style='width: 50%;text-align: start'>Std Dev</td><td style='width: 50%;text-align: end'>"+(toolTipData.Std ? toolTipData.Std : '-')+"</td></tr>" +
                "<tr style='width:"+(colWi)+"px; height: 40px;'><td style='width: 50%;text-align: start'>Unique</td><td style='width: 50%;text-align: end'>"+(toolTipData.Unique ? toolTipData.Unique : '-')+"</td></tr>" +
                "<tr style='width:"+(colWi)+"px; height: 40px;'><td style='width: 50%;text-align: start'>Missing</td><td style='width: 50%;text-align: end'>"+(toolTipData.Missing ? toolTipData.Missing : '-')+"</td></tr>" +
                "</table>");
        }
    };

    onColumnMouseOut =()=>{
        d3.select(".virtualized-table-" +this.state.tableId+ " .ReactVirtualized__Grid__innerScrollContainer").selectAll(".table-toolTip").remove();
    };

    onHeaderClick =(e)=>{
        const ClassMethod = this;
        const { columns, isChartView, headerSelectedKey, tableId } = this.state;
        if(!isChartView){
            if(headerSelectedKey === e.dataKey){
                this.setState({headerSelectedKey: null});
                d3.select(".virtualized-table-" +this.state.tableId+ " .ReactVirtualized__Grid__innerScrollContainer")
                    .selectAll(".table-header-toolTip").remove();
            }else{
                this.setState({headerSelectedKey: e.dataKey}, ()=>{
                    d3.select(".virtualized-table-" +tableId+ " .ReactVirtualized__Grid__innerScrollContainer")
                        .selectAll(".table-header-toolTip").remove();
                    d3.select(".virtualized-table-" +tableId+ " .ReactVirtualized__Grid__innerScrollContainer")
                        .selectAll(".table-toolTip").remove();

                    const colWi = this.calculateColumnWidth();
                    const sortColumnId = 'sortColumnId-'+generateRandomId();
                    const deleteColumnId = 'deleteColumnId-'+generateRandomId();
                    let ind = columns.find((v)=>v.dataKey === e.dataKey).index;

                    let tooltipDiv = d3.select(".virtualized-table-" +tableId+ " .ReactVirtualized__Grid__innerScrollContainer")
                        .selectAll('.ReactVirtualized__Table__row:first-of-type')
                        .append("div").attr("class", "table-header-toolTip");
                    tooltipDiv.style("left", ((ind*colWi))+"px");
                    tooltipDiv.style("top", "-10px");
                    tooltipDiv.style("display", "inline-block");
                    tooltipDiv.html("<table style='width:"+(colWi-10)+"px;background-color: rgba(130, 128, 128, 0.13)'>" +
                        "<tr style='height: 30px;font-size: 16px'>" +
                        "<td style='width: 50%;'><span id='"+sortColumnId+"' style='cursor: pointer'>[+]</span></td>" +
                        "<td style='width: 50%;border-left: 1px solid #BDBDBD'><span id='"+deleteColumnId+"' style='cursor: pointer'>X</span></td>" +
                        "</tr>" +
                        "</table>");
                    d3.select("#"+sortColumnId).on('click', function () {
                        ClassMethod.setState({newSelectedLabel:null, headerSelectedKey:null},()=>{
                            d3.select(".virtualized-table-" +tableId+ " .ReactVirtualized__Grid__innerScrollContainer").selectAll(".table-header-toolTip").remove();
                            ClassMethod.props.onSelectedLabel(e.dataKey);
                        });
                    });
                    d3.select("#"+deleteColumnId).on('click', function () {
                        let newItem = {}; newItem[e.dataKey] = columns.find((v)=>v.dataKey === e.dataKey).type;
                        ClassMethod.setState({newSelectedLabel:null, headerSelectedKey:null},()=>{
                            d3.select(".virtualized-table-" +tableId+ " .ReactVirtualized__Grid__innerScrollContainer").selectAll(".table-header-toolTip").remove();
                            ClassMethod.props.onRemoveColumn(e.dataKey);
                        });
                    });
                });
            }
        }else{
            this.setState({headerSelectedKey : e.dataKey});
        }
    };

    _onUndo = (e) => {
        this.props.onUndo && this.props.onUndo();
    }

    calculateColumnWidth = () => {
        const { width} = this.state;
        return parseInt((width - 50) / this._getDisplayColumns());
    };

    calculateTableWidth = () => {
        const widths = this.state.columns.map(column => column.width);
        if(widths.length > 0){
            return widths.reduce((width1, width2) => width1 + width2);
        }
        return this.state.width;
    };

    initilizeTableDims () {
        this.scrollInterval = setInterval(() => {
            const {rowHeight, pageItem, tbFirstHeight, tableId, miniChart, tbFirstColumn} = this.state;
            const totalWidth = this.calculateTableWidth();
            let scrollContainer = document.querySelector(`.virtualized-table-${tableId} .ReactVirtualized__Grid__innerScrollContainer`);
            let headerRow = document.querySelector(`.virtualized-table-${tableId} .ReactVirtualized__Table__headerRow`);
            if(scrollContainer) {
                scrollContainer.style.minWidth = totalWidth + 'px';
                scrollContainer.style.height = (rowHeight * pageItem) + (miniChart ? tbFirstHeight : 0) +'px';
                scrollContainer.style.maxHeight = (rowHeight * pageItem) + (miniChart ? tbFirstHeight : 0) +'px';
            }
            if(headerRow) {
                headerRow.style.minWidth = headerRow.style.width || totalWidth + 'px' ;
                if(miniChart && scrollContainer){
                    const firstRow = scrollContainer.querySelector('.ReactVirtualized__Table__row');
                    firstRow.style.height = tbFirstHeight+'px';
                    const selectTableFirstRow = d3.select(".virtualized-table-"+tableId+" .ReactVirtualized__Table__row:nth-child(1)");
                    selectTableFirstRow.selectAll(".ReactVirtualized__Table__rowColumn").classed("ReactVirtualized__Table__row-border-left", true);
                    selectTableFirstRow.select(".ReactVirtualized__Table__rowColumn:nth-child("+(tbFirstColumn + 1)+")").classed("ReactVirtualized__Table__row-border-left", false);
                }
            }
            clearInterval(this.scrollInterval);
        }, 1000)
    }

    initilizeTableDimsFirst () {
        const {rowHeight, pageItem, tbFirstHeight, tableId, miniChart, tbFirstColumn} = this.state;
        const totalWidth = this.calculateTableWidth();
        let scrollContainer = document.querySelector(`.virtualized-table-${tableId} .ReactVirtualized__Grid__innerScrollContainer`);
        let headerRow = document.querySelector(`.virtualized-table-${tableId} .ReactVirtualized__Table__headerRow`);
        if(scrollContainer) {
            scrollContainer.style.minWidth = totalWidth + 'px';
            scrollContainer.style.height = (rowHeight * pageItem) + (miniChart ? tbFirstHeight : 0) +'px';
            scrollContainer.style.maxHeight = (rowHeight * pageItem) + (miniChart ? tbFirstHeight : 0) +'px';
        }
        if(headerRow) {
            headerRow.style.minWidth = headerRow.style.width || totalWidth + 'px' ;
            if(miniChart && scrollContainer){
                const firstRow = scrollContainer.querySelector('.ReactVirtualized__Table__row');
                firstRow.style.height = tbFirstHeight+'px';
                const selectTableFirstRow = d3.select(".virtualized-table-"+tableId+" .ReactVirtualized__Table__row:nth-child(1)");
                selectTableFirstRow.selectAll(".ReactVirtualized__Table__rowColumn").classed("ReactVirtualized__Table__row-border-left", true);
                selectTableFirstRow.select(".ReactVirtualized__Table__rowColumn:nth-child("+(tbFirstColumn + 1)+")").classed("ReactVirtualized__Table__row-border-left", false);
            }
        }
        this.initilizeTableDims();
    }

    handlePageClick(currentPage) {
        this.setState({selectedPage : currentPage.selected + 1}, ()=>{
            this.setStateListAndColumnData();
        });
    }

    renderFullChart = () => {
        const { tableId, columns, histData, barData, rowHeight, pageItem, tbFirstHeight, headerSelectedKey } = this.state;
        const displayColumns = this._getDisplayColumns();
        const colVal = columns.find((n)=>n.dataKey === headerSelectedKey);
        const width = displayColumns * colVal.width;
        const height = (rowHeight * pageItem) + tbFirstHeight;

        const chartType = colVal.type === 'N' ? 'histo' : 'bar';
        const chartData = colVal.type === 'N' ? histData.find(v => v.cols === headerSelectedKey).data : barData.find(v => v.cols === headerSelectedKey).data;
        const chartProp = {mode:"full", width:width*70/100, height:height*60/100, chart_type:chartType, chartData:chartData, columnName: headerSelectedKey}

        d3.select(".ReactVirtualized__Grid__innerScrollContainer")
            .selectAll('.ReactVirtualized__Table__row:first-of-type').selectAll(".table-header-toolTip").remove();
        const headActClass = document.querySelector(`.virtualized-table-${tableId} .ReactVirtualized__Table__headerRow .header-active`);
        !!headActClass && headActClass.classList.remove('header-active');
        const headerSelecedColumn = document.querySelector(`.virtualized-table-${tableId} .ReactVirtualized__Table__headerRow`)
            .querySelector('.ReactVirtualized__Table__headerColumn[aria-label="'+headerSelectedKey+'"]');
        headerSelecedColumn.classList.add('header-active');

        let toolTipData = null;
        Object.keys(this.props.data.summary_stats).forEach((v) => {
            if(this.props.data.summary_stats[v]['Column Name'] === headerSelectedKey){
                toolTipData = this.props.data.summary_stats[v];
            }
        });

        const boxPlotProp = {width:width*70/100, height:height*39/100, columnName:headerSelectedKey, matrix_data:this.props.data.box[headerSelectedKey]}
        let mainArr = ["Mean", "Median", "Min", "Max", "Std", "Unique", "Missing", "Column Name"];
        return ( <div style={{backgroundColor:'rgba(189, 189, 189, 0.34)', width:width, height:height}}>
            <div style={{float:'left', width:width*25/100, height:height, maxHeight:height, overflowY:'hidden',overflowX:'hidden', background:'#fff', border:'1px solid #BDBDBD', borderTop:'0px none', boxSizing: 'border-box' }}>
                <table style={{width:(width*25/100),  color:'#757575', height:height, fontSize:'12px'}}>
                    <tbody>
                    <tr style={{height:'30px'}}>
                        <td style={{width: '100%', textAlign:'start',border:'none',outline:'none'}}>Summary</td>
                    </tr>
                    {
                        Object.keys(toolTipData).map((m, indx) =>
                            mainArr.indexOf(m) > -1 && m !== 'Column Name' ?
                                <tr style={{height:'30px'}} key={indx}>
                                    <td style={{width: ((width*25/100)/2)-4, textAlign:'start',float:"left",lineHeight: "3",border:'none',outline:'none'}}>{m === 'Std' ? 'Std Dev' : m}</td>
                                    <td style={{width: ((width*25/100)/2)-4, textAlign:'end',float:"left",lineHeight: "3",border:'none',outline:'none'}}>{(toolTipData[m] ? toolTipData[m] : '-')}</td>
                                </tr> :null)
                    }
                    <tr style={{height:'30px'}}>
                        <td style={{width: '100%', textAlign:'start',border:'none',outline:'none'}}>Top Values</td>
                    </tr>
                    {
                        Object.keys(toolTipData).map((v, ind) =>
                            mainArr.indexOf(v) === -1 ?
                                <tr style={{height:'30px'}} key={ind}>
                                    <td style={{width: ((width*25/100)/2)-4, textAlign:'start',float:"left",lineHeight: "3",border:'none',outline:'none'}}>{v}</td>
                                    <td style={{width: ((width*25/100)/2)-4, textAlign:'end',float:"left",lineHeight: "3",border:'none',outline:'none'}}>{(toolTipData[v] ? toolTipData[v] : '-')}</td>
                                </tr> :null)
                    }
                    </tbody>
                </table>
            </div>
            <div style={{float:'right', width:width*74/100, height:height*60/100, background:'#fff' }}>
                <ChartComponent {...chartProp} bookmarked={this.state.bookmarked} onBookmarked={this.bookmarkedTableChart.bind(this)} />
            </div>
            <div style={{float:'right', width:width*74/100, height:height*39/100, background:'#fff', marginTop: height*1/100}}>
                <BoxPlotTable {...boxPlotProp} />
            </div>
        </div>)
    }

    bookmarkedTableChart=(type, key, data)=>{
        let book = this.state.bookmarked;
        if(type === 'BOOKMARK'){
            book.push(data);
        }
        if(type === 'UNBOOKMARK'){
            book = book.filter((v)=> v.columnName !== key)
        }
        this.setState({isChartView:false, bookmarked:book}, ()=>{
            this.props.onBookmarkedChart(book);
            this.setState({isChartView:true});
        })
    }

    render () {
        const summary_row = !!this.props.data && this.props.data.summary_row ? this.props.data.summary_row : null
        const {isShow, selectedPage, listData, list, columns, deletedColumn, width, headerHeight, rowHeight, newSelectedLabel, pageItem, tbFirstHeight, isChartView, miniChart} = this.state;
        if(!isShow || columns.length < 1){
            return (null);
        }
        const displayColumns = this._getDisplayColumns();
        const tableHeight = headerHeight + (rowHeight * pageItem) + (miniChart ? tbFirstHeight : 0);
        const totalHeight = tableHeight + 100;
        const totalWidth = columns.length > 0 ? this.calculateTableWidth() : width;
        const tableMaxWidth = columns.length > 0 ? this.calculateColumnWidth() * displayColumns : width;
        const rowStyle = {minWidth: totalWidth, paddingRight:'15px'};
        return (
            <div style={{width:width, height:totalHeight, margin:'auto', textAlign:'center'}}>
                <div style={{width:width, height:30, position:'relative', zIndex:'999'}}>
                    { !!deletedColumn && !!deletedColumn.length > 0 && !isChartView ?  <span className="undo-icon" onClick={this._onUndo.bind(this)}> <img alt={'Undo'} width="20px" src={`${process.env.PUBLIC_URL}/extensions/MWEx/build/undo-image.png`} /> </span>
                        :  <span className="undo-icon"> <img width="20px" alt={'Undo'} src={`${process.env.PUBLIC_URL}/extensions/MWEx/build/undo-image-dis.png`} /> </span> }
                    <span style={{float:'left',marginLeft:'85px',marginTop:'5px'}}>{ !!newSelectedLabel && 'Label Selected: '+newSelectedLabel }</span>
                    <span className="headerIcon-span-left" onClick={this.onClickScroll.bind(this, 'right')}> <img width="20px" alt={'Scroll'} src={`${process.env.PUBLIC_URL}/extensions/MWEx/build/left-sc-img.png`} /> </span>
                    <span className="headerIcon-span-right" onClick={this.onClickScroll.bind(this, 'left')}> <img width="20px" alt={'Scroll'} src={`${process.env.PUBLIC_URL}/extensions/MWEx/build/right-sc-img.png`} /> </span>
                    { !!summary_row && <div className={'table-wrap-first-right'}>
                        <label className={'table-wrap-label'}>{ summary_row.Rows+' Rows' }</label>
                        <label className={'table-wrap-label'}>{ summary_row.Columns+' Columns' }</label>
                        { this.props.useExternal ? <label>{summary_row.fileOriginalName}</label> :'-'}
                    </div> }
                </div>
                <div style={{width:tableMaxWidth, height:totalHeight, margin:'auto'}}>
                    <AutoSizer disableHeight={false}>
                        {({width}) => {
                            return ( <div>
                                <Table
                                    className={`virtualized-table-${this.state.tableId}`}
                                    gridClassName="virtulized-scroll"
                                    width={width}
                                    height={tableHeight}
                                    rowHeight={rowHeight}
                                    headerHeight={headerHeight}
                                    rowStyle={rowStyle}
                                    rowCount={list.length || 0}
                                    rowGetter={({ index }) => list[index]}
                                    onHeaderClick={this.onHeaderClick.bind(this)}
                                    overscanRowCount={5}
                                >
                                    { columns.map((column) => <Column
                                        key={generateRandomId()}
                                        {...column}
                                        headerStyle={{minWidth: column.width, paddingRight:'0px'}}
                                    />)}
                                </Table>
                                { list.length > 0 && !isChartView ?
                                    <div style={{width: tableMaxWidth}} className={'table-pagination'}>
                                        <ReactPaginate
                                            previousLabel={"previous"}
                                            nextLabel={"next"}
                                            pageCount= {Math.ceil(listData.length / pageItem)}
                                            forcePage={selectedPage - 1}
                                            marginPagesDisplayed={1}
                                            pageRangeDisplayed={5}
                                            onPageChange={this.handlePageClick.bind(this)}
                                            containerClassName={"pagination"}
                                            subContainerClassName={"pages pagination"}
                                            activeClassName={"active"} />
                                    </div> : null }
                                { isChartView ? <div style={{width:tableMaxWidth, height:'inherit'}}>
                                    <span className="backToTable-chart" onClick={this.backToTable.bind(this)}> X </span>
                                    { this.renderFullChart() }
                                </div> : null }
                            </div>)}}
                    </AutoSizer>
                </div>
            </div>)
    }
}

export default VirtualizedTable;
