import { Component } from "react";
import React from "react";
import {BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table';
import 'react-bootstrap-table/dist/react-bootstrap-table-all.min.css';
import './SelectLabelTable.css';
import { Row,Col } from 'reactstrap'

class SelectLabelTable extends Component {

  constructor(props) {
      super(props);
      this.state = {
          selectedColumns : [],
      }
  }
  onRowSelect=(row, isSelected, e)=> {
        let {selectedColumns = []} = this.state;
        if(isSelected) {
          // push to array
            selectedColumns.push(row.value);

        } else {
            // remove from array
            selectedColumns = selectedColumns.filter((x) => x !== row.value);
        }

        this.setState({selectedColumns}, ()=> {
            this.props.onSelectLabel(this.state.selectedColumns);
        });
    }

    onSelectAll=(isSelected)=> {
        let {selectedColumns = []} = this.state;
        const { internalColumns = []}= window.mwExtVariable;
         if (isSelected) {
             // push all rows
             this.props.onSelectLabel(internalColumns);
             selectedColumns.push(...internalColumns);

        } else {
            // remove all data
            selectedColumns = [];
        }

        this.setState({selectedColumns}, ()=> {
            console.log('updated state', this.state.selectedColumns);
            this.props.onSelectLabel(this.state.selectedColumns);
        });
           return  internalColumns;

    }

    componentWillUnmount() {
    this.setState({
        selectedColumns:[]
    })
}
    render() {
        const{ internalColumns = [] } = window.mwExtVariable;
        //const internalColumns =[];
        //internalColumns.push({value: window.mwExtVariable});
        //console.log( internalColumns)
        //let internalColumns = ["id", "crim", "zn", "indus", "chas", "nox", "rm", "age", "dis", "rad", "tax", "ptratio", "black", "lstat", "medv"]
        let internalColumnsData=[];
        internalColumns && internalColumns.map((val,i)=>{
            var obj={
                value:val
                }
            internalColumnsData.push(obj);
            return val;
        })
        const selectRowProp = {
            mode: 'checkbox',
            clickToSelect: true,
            onSelect: this.onRowSelect,
            onSelectAll: this.onSelectAll
        };
        const options = {
            sizePerPage: 10,  // which size per page you want to locate as default
            paginationSize: 3,  // the pagination bar size.
            prePage: 'previous', // Previous page button text
            nextPage: 'next', // Next page button text
        };

        return (
                <Row>
                    <Col md={12}>
                        <BootstrapTable
                            ref='table'
                            className="selectedTable"
                            data={internalColumnsData}
                            options={ options }
                            pagination={ true }
                            striped
                            hover
                            selectRow={ selectRowProp }>
                            <TableHeaderColumn isKey dataField='value'>Fields</TableHeaderColumn>
                        </BootstrapTable>
                    </Col>
                </Row>
        );
    }
}

export default SelectLabelTable;
