import React, { Component } from "react";
import { Progress } from 'reactstrap';
import Dropzone from "react-dropzone";
import './fileUploader.css';

class FileUploader extends Component {
    constructor(props) {
        super(props);
        this.state = {
            uploading: false,
            uploadedFileName: null,
            fileProgressed: 0,
            showSelectTable:false
        }
    }

    openDialog = () => {
        this.uploadFile && this.uploadFile.open();
    };

    onDrop = (files) => {
        let file = files[0];
        this.setState({uploadedFileName: file.name, uploading: true, fileProgressed: 0}, ()=>{
            this.props.onDrop && this.props.onDrop(file, this.onProgressUpload, this.getCancelSource);
            this.uploadInterval = setInterval(()=>{
                const { fileProgressed, uploading } = this.state;
                if(fileProgressed > 99 || !uploading){
                    clearInterval(this.uploadInterval);
                }else{
                    this.setState({ fileProgressed: (fileProgressed + 0.20) });
                }
            }, 5);
        });
    };

    onProgressUpload = (flag) => {
        if(flag) {
            clearInterval(this.uploadInterval);
            this.completeInterval = setInterval(()=>{
                const { fileProgressed } = this.state;
                if(fileProgressed > 99){
                    clearInterval(this.completeInterval);
                    this.setState({ uploading: false },()=>{this.props.onComplete && this.props.onComplete()});
                }else{
                    this.setState({ fileProgressed: (fileProgressed + 0.50) });
                }
            }, 5);
        } else {
            this.setState({ uploading: false }, ()=>{
                clearInterval(this.uploadInterval);
                clearInterval(this.completeInterval);
            });
        }
    };

    getCancelSource = (source) => {
        this.cancelSource = source;
    };

    onCancelRequest = () => {
        this.setState({ uploading: false }, ()=>{
            clearInterval(this.uploadInterval);
            clearInterval(this.completeInterval);
            this.cancelSource && this.cancelSource.cancel();
        });
    };

    componentWillUnmount(){
        clearInterval(this.uploadInterval);
        clearInterval(this.completeInterval);
    }

    render() {
        const { uploading, fileProgressed, uploadedFileName } = this.state;
        const styles = !!this.props.isDropzone && this.props.isDropzone ?
            {cursor:'pointer',outline:'none',height:'50px',border:'1px dashed #bdbdbd',textAlign:'center',lineHeight:'1em',color:'rgb(117, 117, 117)',background:'#e4e4e4',fontFamily:"Roboto, sans-serif"}
            : {display:'none'};
        return (
            <>
                {
                    uploading === true  ?
                        <div className={'progress-upload-container'}>
                            <img style={{width:'4%', margin:'8px'}} alt={'Loading'} src={`${process.env.PUBLIC_URL}/extensions/MWEx/build/loader.svg`} />
                            <label title={uploadedFileName} className={'progress-label'}>{uploadedFileName}</label>
                            <Progress className={'uploaded-progress'} value={fileProgressed} />
                            <img onClick={this.onCancelRequest.bind(this)} className={'cancel-icon'} alt={'Cancel'} src={`${process.env.PUBLIC_URL}/extensions/MWEx/build/cancel-50.png`} />
                        </div>
                        :
                        <Dropzone onDrop={this.onDrop.bind(this)} ref={(ref) => this.uploadFile = ref}>
                                {({getRootProps, getInputProps}) => {
                                return (
                                    <div {...getRootProps()} style={styles}>
                                        <input {...getInputProps()} />
                                        <p>Drag a file or browse to upload</p>
                                    </div>
                                )
                            }}
                        </Dropzone>
                }
            </>
        );
    }
}

export default FileUploader;
