import { Component } from "react";
import React from "react";
import './stepper.css';

class Stepper extends Component {

    constructor(props) {
        super(props);
        this.state = {
            step: 0,
            classes: ['active', '', ''],
            evelResults:null,
            predicted:false,
            label:null
        }
    }

    componentWillReceiveProps(nextProps){
      if(nextProps.step !== this.state.step){
            this.onStepChange(nextProps.step);
         }
    }

    switchActiveState = (step) => {
        if(step <= this.state.step){
            this.props.onClickStep(step);
            this.setState({label:this.props.stepperData.executeModelRequest && this.props.stepperData.executeModelRequest.label})
        }
    };
    selectLableTableBtn = (step) => {
        if(step <= this.state.step){
            this.props.onSelectStep(step);
        }
    };

    switchActiveResultView = (step) => {
      if(step <= this.state.step){
        this.props.backOnResultView(step);
    }
    };
    switchActiveTableView = (step) => {
            this.props.backOnTableView(step);

    };

    switchToTrainedView = (label) => {
        this.props.forwardOnTrainedView(label);
    }
    switchToUploadView = () => {
        this.props.backOnUploadView();
    }

     switchActivePredictedView = (step,e,label) => {
            this.props.backOnPredictedView(this.state.predicted,label);

    };

    onStepChange(index) {
        let {classes} = this.state;
        classes  = classes.map((value, i) => {
            if(i < index) {
                value = 'visited';
            } else if( i === index) {
                value = 'active';
            } else {
                value = '';
            }
            return value;
        });
        this.setState({classes, step: index});
    }
    render() {
        const {classes ,step } = this.state;
        const label = this.props.stepperData.executeModelRequest.label;
        const newLabel = this.props.selectedLabel
        const { useExternal } =  window.mwExtVariable;
        return (
            <div className="stepper-main">
                <div className="md-stepper-horizontal orange">
                    <div className={`md-step ${classes[0]} done`}>
                        <div style={{textAlign:'center'}}><img alt={'Upload'} src={`${process.env.PUBLIC_URL}/extensions/MWEx/build/ICON1.png`} /></div>
                        {/*<div className="md-step-circle"><span>1</span></div>*/}
                        <div className="md-step-title">Upload your training file</div>
                        <div className="md-step-title">{useExternal ? <button onClick={(e) => this.switchActiveState(0, e)} className="btn">BROWSE</button> : <button className="btn" onClick={(e) => this.selectLableTableBtn(0, e)}>UPLOAD</button>}</div>
                        <div className="md-step-bar-left"/>
                        <div className="md-step-bar-right"/>
                    </div>
                    <div className={`md-step ${classes[1]} editable`}>
                        <div style={{textAlign:'center'}}><img alt={'Train'} src={`${process.env.PUBLIC_URL}/extensions/MWEx/build/Group5.png`} /></div>
                        {/*<div className="md-step-circle"><span>2</span></div>*/}
                        <div className="md-step-title">Model building</div>
                        <div className="md-step-title">
                            {(step===2 && this.props.stepperData.evelResults) || (this.props.stepperData.evelResults && step===1) || (this.props.stepperData.predicted===true && step===2)?
                            <button
                                className={`back-btn-stepper`}
                                onClick={
                                    step === 1 ?
                                        (e)=> this.switchActiveTableView(0,e)
                                        :
                                        step===2 && this.props.stepperData.predicted===false ?
                                            (e) => this.switchActivePredictedView(2,e,label)
                                            :
                                            (e) => this.switchActiveResultView(2,e)}
                            />
                            :null}
                            <button
                                className="btn"
                                onClick={(e) => this.switchActiveState(1, e)}
                            >
                                TRAIN
                            </button>
                            {
                                !this.props.stepperData.loading && !!this.props.stepperData.tempEvelResults && (label && label) === newLabel ?
                                <button
                                    id="1"
                                    className={`back-btn-Prediction`}
                                    onClick={
                                        (e)=>this.switchToTrainedView()}
                                />
                                :
                                step ===  1 && !!this.props.stepperData.predicted===false && this.props.stepperData.evelResults && this.props.stepperData.predictFileName && (label && label) === newLabel ?
                                    <button
                                        id="2"
                                        className={`back-btn-Prediction`}
                                        onClick={this.switchToUploadView}
                                    />
                                    :
                                    null
                            }
                        </div>
                        <div className="md-step-bar-left"/>
                        <div className="md-step-bar-right"/>
                    </div>
                    <div className={`md-step ${classes[2]}`}>
                        <div style={{textAlign:'center'}}><img alt={'Predict'} src={`${process.env.PUBLIC_URL}/extensions/MWEx/build/ICON3.jpg`} /></div>
                        {/*<div className="md-step-circle"><span>3</span></div>*/}
                        <div className="md-step-title">Make Predictions</div>
                        <div className="md-step-title"><button className="btn" onClick={(e) => this.switchActiveState(2, e)}>PREDICT</button></div>
                        <div className="md-step-bar-left"/>
                        <div className="md-step-bar-right"/>
                    </div>
                </div>
            </div>
        );
    }
}

export default Stepper;
