import { Component } from "react";
import React from "react";
import './stepper.css';

class ContentStepper extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            activate: 0,
            steps: ["Data Loading", "Preprocessing", "Fitting", "Evaluation", "Scoring and Publication"],
            classes: ["active", "", "", "", ""],
            intervalVar:0
        }
    }
    componentDidMount() {
        this._isMounted = true;
    }

    componentWillReceiveProps(nextProps) {
        if(nextProps.responseData !== this.props.responseData){
            let responseData = nextProps.responseData;
            if(responseData) {
                responseData.item && responseData.item.models.forEach((val,i)=>{
                    val.statuses.forEach((val1,i)=>{
                        if(val1.status==="Complete") {
                            if(this.state.steps.indexOf(val1.step) > this.st){
                                this.onPollingLoading1();
                                this.st = this.state.steps.indexOf(val1.step);
                            }
                        }
                    })
                })
            }
        }
    }

    onPollingLoading = () => {
        if (this._isMounted) {
            this.st = 0;
            this.setState({loading: true, activate: 0, classes: ["active", "", "", "", ""]}, ()=> {

                this.props.onStart && this.props.onStart();
            });
        }
        ;
    }

    onPollingLoading1 = () => {
        this.setTimeUploadInterval = setInterval(()=>{
            const {steps,activate, classes, loading} = this.state;
            if(loading){
                if(steps.length > activate+1 && this.st + 1 > activate){
                    let classes2  = classes.map((value, i) => {
                        if(i < activate+1) {
                            value = 'done';
                        } else if( i === activate+1) {
                            value = 'active';
                        } else {
                            value = '';
                        }
                        return value;
                    });
                    if (this._isMounted) {
                        this.setState({classes: classes2, activate: activate + 1});
                    }
                }
            }else {
                clearInterval(this.setTimeUploadInterval);
                this.onStopLoading();
            }
        }, 2000)
    };

    onLoading = () => {
        this.setState({loading:true, activate: 0, classes: ["active", "", "", "", ""]}, ()=>{
            this.props.onStart && this.props.onStart();
            this.setTimeInterval = setInterval(()=>{
                const {activate, steps, classes, loading} = this.state;
                if(loading){
                    if(steps.length !== (activate + 1)){
                        let classes2  = classes.map((value, i) => {
                            if(i < (activate + 1)) {
                                value = 'done';
                            } else if( i === (activate + 1)) {
                                value = 'active';
                            } else {
                                value = '';
                            }
                            return value;
                        });
                        if (this._isMounted) {
                            this.setState({classes: classes2, activate: (activate + 1)});
                        }
                    }
                }else {
                    this.cancel();
                }
            }, 3000)
        });
    };

    onStopLoading = () => {
        if (this._isMounted) {
            this.setState({loading: false}
                , ()=> {
                    clearInterval(this.setTimeInterval);
                });
        } ;
    }

    cancel = () => {
        this.onStopLoading();
        this.props.onCancel && this.props.onCancel();
    };

    componentWillUnmount(){
        this._isMounted = false;
        clearInterval(this.setTimeInterval);
     }
   render() {
        const { steps, classes, loading } = this.state;
        return (
            <div>
            {
                loading === true ?
                    <div>
                        <div className="content-stepper-horizontal orange">
                            <div className={`content-step ${classes[0]}`}>
                                <div className="content-step-title">{steps[0]}</div>
                                <div className="content-step-bar-left"/>
                                <div className="content-step-bar-right"/>
                            </div>
                            <div className={`content-step ${classes[1]}`}>
                                <div className="content-step-title">{steps[1]}</div>
                                <div className="content-step-bar-left"/>
                                <div className="content-step-bar-right"/>
                            </div>
                            <div className={`content-step ${classes[2]}`}>
                                <div className="content-step-title">{steps[2]}</div>
                                <div className="content-step-bar-left"/>
                                <div className="content-step-bar-right"/>
                            </div>
                            <div className={`content-step ${classes[3]}`}>
                                <div className="content-step-title">{steps[3]}</div>
                                <div className="content-step-bar-left"/>
                                <div className="content-step-bar-right"/>
                            </div>
                            <div className={`content-step ${classes[4]}`}>
                                <div className="content-step-title">{steps[4]}</div>
                                <div className="content-step-bar-left"/>
                                <div className="content-step-bar-right"/>
                            </div>
                        </div>

                        <div className="stepper-horizontal-container">
                            <img alt="Loading..." src={`${process.env.PUBLIC_URL}/extensions/MWEx/build/loader.svg`} width={80} />
                            <p style={{color:'#757575'}} />
                            <button className={'btn-cancel'} onClick={this.cancel.bind(this)}>Cancel</button>
                        </div>
                    </div>
                    :
                    null
            }
            </div>
        );
    }
}

export default ContentStepper;
