import React, { Component } from 'react';
import Stepper from "./components/stepper/stepper";
import Container from "./components/container/container";
import axios from 'axios';
import VirtualizedTable from './components/table/table';
import FileUploader from "./components/fileUploader/fileUploader";
import ErrorContainer from "./components/common/error";
import SelectLabelTable from "./components/SelectLabelTable/SelectLabelTable";
import ContentStepper from "./components/stepper/content-stepper";
import BarChartComponent from "./components/chart/barChart";
import uuid from 'uuid';

axios.defaults.timeout = 60000;
class App extends Component {
    interval = null;
    constructor(props) {
        super(props);
        this.state =  {
            windowWidth: 1000,
            windowHeight: 600,
            token:null,
            step: 0,
            error:null,
            loading: false,
            runAlgoFlag:false,
            predictFileName:null,
            evelResults:null,
            tempEvelResults:null,
            predicted:false,
            tableData: {fileUploadData:null,chartData:null,selectedLabel:null,deletedColumn:[],bookmarked:[]},
            executeModelRequest: {runId:null,label:null,features:[],predictionFlag:null,kfold:0,runOption:"A",
                evalMetric:"mean_squared_error",algorithms:window.mwExtVariable.algorithm,toggleValue:false,algoSettings:[],
                hyperParameterFlag:false,pcaFlag:false,pcaValue:0.95,advanceAlgoSettings:true,trainTestSplit:0.3},
            executeModelResult:null,
            responseData:null,
            selectedLabelValue:null,
            useExternal : window.mwExtVariable.useExternal,
            interactionState : window.mwExtVariable.options.interactionState,
            showSelectTable: true,
            internalColumns:window.mwExtVariable.internalColumns,
            storageHrefLink:null,
            showVirtualTable: false
        };
    }

    handleResize = () => this.setState({
        windowHeight: window.innerHeight,
        windowWidth: window.innerWidth
    });

    componentWillUnmount() {
        window.mwExtVariable.flag1=1;
        this.interval.clearInterval();
        window.removeEventListener('resize', this.handleResize);
        this._isMounted = false;
    }

    componentDidMount() {
        this._isMounted = true;
       this.handleResize();
        window.addEventListener('resize', this.handleResize);

        this.interval = setInterval(() => {
            this.setState({
                useExternal:window.mwExtVariable.useExternal,
                interactionState:window.mwExtVariable.options.interactionState
            })
        }, 1);
    }
    loginApp = () => {
        let {username, password, apiUrl} = window.mwExtVariable;
        const config = {
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            }
        };
        const form = `email=${username}&password=${password}`;
        return	new Promise((resolve, reject) => {
            axios.post(apiUrl+'loginUser', form, config)
                .then((response) => {
                    if(response.data.message && response.data.message.token){
                        this.setState({token: response.data.message.token,storageHrefLink:response.data.message.hyperLink !== null ? response.data.message.hyperLink :''},() => {
                            resolve();
                        });
                    } else {
                        reject();
                    }
                })
                .catch((err) => {
                    reject(err);
                })
        });
    };

    _callingPostAPIs = (path, data, config, onProgress, getCancelSource) => {
        const { apiUrl } = window.mwExtVariable;
        const selfThis = this;
        return new Promise(function (resolve, reject){
            let CancelToken = axios.CancelToken;
            let source = CancelToken.source();
            config.cancelToken = source.token;
            getCancelSource && getCancelSource(source);
            axios.post(apiUrl+path, data, config)
                .then((response) => {
                    if((response.data.message !== null || response.data.message !== "") && typeof response.data.message === 'object'){
                        resolve(response.data.message);
                    }else{
                        selfThis._setErrorMessage(response.data.message);
                        onProgress && onProgress(false);
                        reject(response.data.message);
                    }
                }).catch((e) => {
                    selfThis._setErrorMessage(e);
                    onProgress && onProgress(false);
                    reject(e);
                })
        });
    };

    _setErrorMessage(e) {
        let error = {};
        const { step } = this.state;
        if(step === 0){
            error.heading = "Error in the uploaded file.";
            error.message = "Please check the file is correct format and try again";
        }else if (step === 1) {
            error.heading = "Error during model build.";
            error.message = "Check to see if the correct label and features were selected";
        }else if (step === 2) {
            error.heading = "Error in prediction.";
            error.message = "something went wrong, try again";
        }

        if(axios.isCancel(e)) {
            error.heading = '';
            error.message = "Request Cancelled";
        }else if (e.code === "ECONNABORTED") {
            error.heading = '';
            error.message = "Request timeout";
        }else{
            if(typeof e === 'string' && e !== ""){
                if('Request Cancelled' === e || 'Authentication Failure' === e){
                    error.heading = '';
                }
                error.message = e;
            }
        }
        this.setState({ error });
    }

    getCancelSource = (source) => {
        this.cancelRequestSource = source;
    };

    onProgress = (flag) => {
        if(!flag) {
            this.setState({loading:false});
            clearInterval(this.setTimeInterval);
            this.contentStepper && this.contentStepper.onStopLoading();
        }
    };

    onStepChange = (index) => {
        this.setState({step: index});
    };

    _onErrorClick = () => {
        this.setState({error: null, loading: false});
    };

    _onClickStep = (index) => {
        const abc =window.mwExtVariable.options.interactionState ;
        const { error, tableData, evelResults, runAlgoFlag } = this.state;
        this.setState({
            showSelectTable:false,
            tempEvelResults:null,
        })
        if(!error && abc===1){
            if(index === 0){
                this.fileUploader && this.fileUploader.openDialog();
            }else if(index === 1 && !!tableData.selectedLabel && !evelResults){
                if(runAlgoFlag){
                    this.setState({loading:true},()=>{
                        this._createNewRun();
                    });

                }else{
                     this.contentStepper && this.contentStepper.onPollingLoading();
                }
            }else if (index === 2){
                this._batchPredictionAPI();
            }
        }
    };
    _onSelectStep = (index,onProgress, getCancelSource) => {
       const { error, tableData, evelResults, runAlgoFlag,selectedLabelValue,interactionState, showSelectTable } = this.state;

        if(!selectedLabelValue || selectedLabelValue.length === 0 ) {
            return;
        }
        if(!showSelectTable) {
            this.setState({showSelectTable : true,step : 0})
            return
        }
        if(interactionState === 1) {
            this.setState({loading:true, showSelectTable: false});
            window.mwExtVariable.getExportedData(selectedLabelValue)
                .then((data) => {
                    var f = new File([data], `${uuid.v4()}.json`, {type: "application/json"})
                    if(!error && interactionState === 1){
                        if(index === 0){
                            this._fileUpload(f,this.onProgress(true), this.getCancelSource);
                        }else if(index === 1 && !!tableData.selectedLabel && !evelResults){
                            if(runAlgoFlag){
                                this.setState({loading:true},()=>{
                                    this._createNewRun();
                                });

                            }else{
                                this.contentStepper && this.contentStepper.onPollingLoading();
                            }
                        }else if (index === 2){
                            this._batchPredictionAPI();
                        }
                    }
                }, (err) => {
                    console.log('error in fetching window.mwExtVariable.getExportedData', err);
                });
        }
    }
    _onSelectLabel = (data) => {
        this.setState({
            selectedLabelValue : data
        })
    }
    _backOnTableView = () => {

          this.setState({evelResults: null, tempEvelResults:this.state.evelResults});

    };

    forwardOnTrainedView = () => {
        this.setState({
            evelResults: this.state.tempEvelResults,
            tempEvelResults:null
        });
    };

    _backOnUploadView = () => {
        this.setState({step:2,evelResults:this.state.evelResults,predicted:false});
    };

    _backOnResultView = () => {
         this.setState({step:2,evelResults:this.state.evelResults,predicted:false});
    };
    _backOnPredictedView = (predicted,label) => {
        this.setState({
            step:1,
            predicted:false,
            predictFileName:label !== this.state.executeModelRequest.label ? null : this.state.predictFileName

        });
    };
    _onTrainingStart = () => {
        this.setState({loading: true},()=>{
            this._predictFlagFromLabelAPI();
            // this.setTimeInterval = setInterval(()=>{
            //     this._processStatusAPI();
            // }, 10000);
        });
    };

    _onTrainingCancelReq = () => {
        this._setErrorMessage("Request Cancelled");
        clearInterval(this.setTimeInterval);
        this.cancelRequestSource && this.cancelRequestSource.cancel();
        this.onProgress(false);
    };

    _onTrainingReqError = () => {
        this._setErrorMessage('');
        clearInterval(this.setTimeInterval);
        this.cancelRequestSource && this.cancelRequestSource.cancel();
    };

    _createNewRun = () => {
        const { executeModelResult, tableData } = this.state;
        const { fileUploadData } = tableData;
        if(!!fileUploadData && !!executeModelResult){
            const config = {
                headers: {
                    "Authorization" : "Bearer "+this.state.token,
                    "Content-Type"  : "application/json"
                }
            };
            let data = {runPayload:{runName:fileUploadData.runName,experimentName:fileUploadData.experimentName,
                    fileName:fileUploadData.file.fileName,fileFormat:fileUploadData.file.fileFormat,userId:executeModelResult.item.userId,
                    fileOriginalName:fileUploadData.file.fileOriginalName,errorFlag:false},runId:fileUploadData.runId};
            this._callingPostAPIs("createNewRun", data, config, null, null)
                .then((res)=>{
                    let _data = JSON.parse(res.csvData);
                    let tableData = { ...this.state.tableData, fileUploadData: res, chartData: {..._data, summary_row: {..._data.summary_row, fileOriginalName: res.file.fileOriginalName}}};
                    this.setState({ tableData, runAlgoFlag:false }, ()=>{ this.contentStepper && this.contentStepper.onPollingLoading(); });
                })
        }
    };

    _fileUpload = (file, onProgress, getCancelSource) => {
        const algorithm  = window.mwExtVariable.algorithm;
        this.setState({ step:0,evelResults:null,predictFileName:null,runAlgoFlag: false,predicted:false,
            tableData: {fileUploadData:null,chartData:null,selectedLabel:null,deletedColumn:[],bookmarked:[]},
            executeModelRequest: {runId:null,label:null,features:[],predictionFlag:null,kfold:0,runOption:"A",
                evalMetric:"mean_squared_error",algorithms:[algorithm],toggleValue:false,algoSettings:[],
                hyperParameterFlag:false,pcaFlag:false,pcaValue:0.95,advanceAlgoSettings:true,trainTestSplit:0.3}},
        ()=>{
            const config = {
                headers: {
                    "Authorization" : this.state.token,
                    "Content-Type"  : "multipart/form-data"
                }
            };
            let form = new FormData();
            form.append("file", file);
            form.append("experimentName", `${algorithm}Experiment`+ uuid.v4());
            if (this.state.token) {
                this._callingPostAPIs("fileUpload", form, config, onProgress, getCancelSource)
                    .then((res)=>{
                        let _data = JSON.parse(res.csvData);
                        this.setState({
                            loading: false,
                            tableData: { ...this.state.tableData,
                            fileUploadData: res,
                            chartData: {..._data,
                            summary_row: {..._data.summary_row, fileOriginalName: res.file.fileOriginalName}} } },
                            () => {
                                onProgress && onProgress(true);
                                setInterval(()=> {
                                    this.setState({showVirtualTable: true});
                                }, 500);

                            });
                    })
            } else {
                this.loginApp().then(() => {
                    this._fileUpload(file, onProgress, getCancelSource);
                }).catch((err) => {
                    onProgress && onProgress(false);
                    this._setErrorMessage(`Authentication Failure`);
                })
            }
        });
    };

    _predictFlagFromLabelAPI = () => {
        let selectAlgoName = window.mwExtVariable.algorithm;
        const { selectedLabel, fileUploadData } = this.state.tableData;
        if(!!fileUploadData && !!selectedLabel){
            let data = {label: selectedLabel,runId: fileUploadData.runId, serverFile: fileUploadData.file};
            const config = {
                headers: {
                    "Authorization" : "Bearer "+this.state.token,
                    "Content-Type"  : "application/json"
                }
            };
            this._callingPostAPIs("predictFlagFromLabel", data, config, this.onProgress, this.getCancelSource)
                .then((res)=>{
                    let algoRes = res.algorithm.filter(item => item.algorithmName === selectAlgoName);
                    if(algoRes[0] === undefined ) {
                        this._setErrorMessage('Error in Selected Algorithm ');
                        this.onProgress && this.onProgress(false);
                     }else{
                        let flagVale = res.evaluationMetric.find(v=>v.DEFAULT_FLAG);
                        if (!!flagVale) {
                            this.setState({
                                executeModelRequest: {
                                    ...this.state.executeModelRequest,
                                    algoSettings: [{
                                        algorithmName: algoRes[0].algorithmName,
                                        settings: algoRes[0].settings.map((set) => {
                                            return {value: set.defaultValue, name: set.name}
                                        })
                                    }] || [],
                                    algorithms: [algoRes[0].algorithmName],
                                    runOption: algoRes[0].algorithmName === "AutoML" ? "A" : "RL",
                                    predictionFlag: flagVale.PREDICTOR_FLAG,
                                    evalMetric: flagVale.PREDICTOR_FLAG === 'C' ? 'accuracy_score' : 'mean_squared_error',
                                    kfold: 10
                                }
                            }, ()=> {
                                this._executeModelAPI();
                            });
                        }
                    }
                });
        }
    };

    _executeModelAPI = () => {
        const { tableData, executeModelRequest } = this.state;
        const { fileUploadData, selectedLabel, deletedColumn } = tableData;
        if(!!fileUploadData && !!selectedLabel){
            let features = [];
            let _data = JSON.parse(fileUploadData.csvData);
            Object.keys(_data.columns).forEach((v) => {
                if(v !== selectedLabel && deletedColumn.indexOf(v) < 0){
                    features.push(v);
                }
            });
            let data = {...executeModelRequest, label: selectedLabel, runId: fileUploadData.runId, features: features};
            window.mwExtVariable.executeModelRequestPayload = data;
            this.setState({executeModelRequest: data}, ()=>{
                const config = {
                    headers: {
                        "Authorization" : "Bearer "+this.state.token,
                        "Content-Type"  : "application/json"
                    }
                };
                this._callingPostAPIs("executeModel", data, config, this.onProgress, this.getCancelSource)
                    .then((res)=>{
                        this.setState({executeModelResult:res},()=>{this._runAlgorithmsAPI();});
                    });
            });
        }
    };

    _runAlgorithmsAPI = () => {
        const { fileUploadData } = this.state.tableData;
        if(!!fileUploadData){
            let data = {runId: fileUploadData.runId};
            const config = {
                headers: {
                    "Authorization" : "Bearer "+this.state.token,
                    "Content-Type"  : "application/json"
                }
            };
            this._callingPostAPIs("runAlgorithms", data, config, this.onProgress, this.getCancelSource)
                .then(res=>this.setState({
                    runAlgoFlag:true },()=>
                     setTimeout(() => {
                        this._processStatusAPI()
                        }, 5000)
                     )

                )
                .catch(e=>this.setState({runAlgoFlag:true}));
        }
    };

    _processStatusAPI = () => {
        const { tableData, executeModelRequest } = this.state;
        const { fileUploadData, selectedLabel } = tableData;
        if(!!fileUploadData && !!selectedLabel){
            let data = {runId: fileUploadData.runId, predictionFlag: executeModelRequest.predictionFlag};
            const config = {
                headers: {
                    "Authorization" : "Bearer "+this.state.token,
                    "Content-Type"  : "application/json"
                }
            };
            this._callingPostAPIs("processStatus", data, config, this.onProgress, this.getCancelSource)
                .then((response)=>{
                    this.setState({responseData:response});
                    if(!!response.item && response.item.complete === true){
                        this._getEvaluationResultsAPI();
                    }
                    else if(response.item === null){
                        this.onProgress(false);
                        this._onTrainingReqError();
                    }
                    else{
                        if(this.state.loading) {
                            setTimeout(() => {
                                this._processStatusAPI()
                            }, 2000)
                        }
                    }
                });
        }
    };

    _getEvaluationResultsAPI = () => {
        clearInterval(this.setTimeInterval);
        const { fileUploadData, selectedLabel } = this.state.tableData;
        if(!!fileUploadData && !!selectedLabel){
            let data = `runId=${fileUploadData.runId}&modelName=${window.mwExtVariable.algorithm}`;
            const config = {
                headers: {
                    "Content-Type"  : "application/x-www-form-urlencoded",
                    "Authorization" : "Bearer "+this.state.token
                }
            };
            this._callingPostAPIs("getEvaluationResults", data, config, this.onProgress, this.getCancelSource)
                .then((response)=>{
                    window.mwExtVariable.getevalResponsePayload = response;
                    if(!!response.evalResults && !!response.evalResults.modelResults){
                        this._deployAPI(response);
                    }else{
                        this.onProgress(false);
                        this._onTrainingReqError();
                    }
                });
        }
    };

    _deployAPI = (evelResults) => {
        const selectAlgoName = window.mwExtVariable.algorithm;
        const { fileUploadData, selectedLabel } = this.state.tableData;
        if(!!fileUploadData && !!selectedLabel){
            let data = {runId:fileUploadData.runId,modelName:selectAlgoName,deployName:fileUploadData.file.fileName+`_${selectAlgoName}.pkl`};
            const config = {
                headers: {
                    "Content-Type"  : "application/json",
                    "Authorization" : "Bearer "+ this.state.token
                }
            };
            this._callingPostAPIs("deploy", data, config, this.onProgress, this.getCancelSource)
                .then((res)=>{
                    window.mwExtVariable.deployResponsePayload = res;
                    this.onProgress(false);
                    this.setState({evelResults,loading:false});
                });
        }
    };

    _uploadTestFile = (file, onProgress, getCancelSource) => {
        let selfThis = this;
        let reader = new FileReader();
        reader.onload = function() {
            let callback = true;
            let headerString = reader.result.split("\n");
            let header = headerString[0].split(",");
            header = header.map(s => s.trim());
            const { executeModelRequest, token } = selfThis.state;

            if(header.length > 0){
                var matchCount = 0;
                executeModelRequest.features.forEach((v) => {
                    var match = header.indexOf(v)
                    if(match >=0 ) {
                        matchCount += 1;
                    }
                })
                if(executeModelRequest.features.length <=  header.length   && matchCount === executeModelRequest.features.length) {
                    callback = false;
                }
                // executeModelRequest.features.forEach((v)=>{
                //     if(header.indexOf(v.trim()) === -1){
                //         callback = true;
                //     }
                // });

                if(callback){
                    onProgress && onProgress(false);
                    selfThis.setState({error:{
                        message:"There was an error in the uploaded file.Please check the file format and also ensure all the required features were available for the model  "}});
                }else{
                    const config = {
                        headers: {
                            "Authorization" : "Bearer " + token,
                            "Content-Type"  : "multipart/form-data"
                        }
                    };

                    let form = new FormData();
                    form.append('file', file);

                    selfThis._callingPostAPIs("uploadFile", form, config, onProgress, getCancelSource)
                        .then((response)=>{
                            selfThis.setState({predictFileName: response.fileName},()=>{
                                onProgress && onProgress(false);
                                selfThis.onStepChange(2);
                            });
                        });
                }
            }

        };
        reader.readAsText(file);
    };

    _batchPredictionAPI = () => {
        this.setState({loading: true, predicted:false}, ()=>{
            const { tableData, executeModelRequest, predictFileName, token ,storageHrefLink} = this.state;
            const { fileUploadData, selectedLabel } = tableData;
            if(!!fileUploadData && !!executeModelRequest && !!selectedLabel){
                let data = {runId:fileUploadData.runId,fileName:predictFileName,label:selectedLabel,features:executeModelRequest.features};
                const config = {
                    headers: {
                        "Content-Type"  : "application/json",
                        "Authorization" : "Bearer " + token
                    }
                };
                this._callingPostAPIs("predictResults", data, config, null, null)
                    .then((res)=>{
                        if(res.status) {
                            window.mwExtVariable.predict_file = storageHrefLink ? storageHrefLink + '/' + predictFileName + '_predictions.csv' : window.mwExtVariable.apiUrl + 'uploads/' + predictFileName + '_predictions.csv';
                            window.mwExtVariable.dataJson = res.data.dataJson;
                            this.setState({loading: false, predicted: true});
                            document.getElementById('predictLoadButton') && document.getElementById('predictLoadButton').click();
                        }else{
                            this.onProgress && this.onProgress(false);
                           this.setState({error:{
                                message:"An error occurred. Please check your file and try again or contact support@machineworksai.com"}});

                        }
                        })
                    .catch(e=>this.setState({loading: false}));
            }
        })
    };

    _onSelectedLabel(labelName){
        if(labelName === "" || labelName === null){
            let tableData = {...this.state.tableData, selectedLabel: null};
            this.setState({tableData,selectedLabel:labelName,evelResults:null,predictFileName:null}, ()=>{ this.onStepChange(0); });
        }else{
            let tableData = {...this.state.tableData, selectedLabel: labelName};
            this.setState({tableData,selectedLabel:labelName,evelResults:null,predictFileName:null}, ()=>{ this.onStepChange(1); });
        }
    }

    bookmarkedChart(bookmarked){
        let tableData = {...this.state.tableData, bookmarked: bookmarked};
        this.setState({tableData});
    }

    _onRemoveColumn(columnName){
        const { chartData, selectedLabel, deletedColumn } = this.state.tableData;
        let data = {};
        Object.keys(chartData.columns).forEach((v)=>{
            if(v !== columnName){
                data[v] = chartData.columns[v];
            }
        });
        let setData = {...chartData, columns: data};
        let delCol = [columnName, ...deletedColumn];
        let tableData = {...this.state.tableData, chartData: setData, deletedColumn: delCol};
        this.setState({tableData,step: selectedLabel ? 1 : 0}, ()=>{
            if(columnName === selectedLabel) {
                this._onSelectedLabel(null);
            }
        });
    }

    _onUndo(){
        const { chartData, deletedColumn, fileUploadData } = this.state.tableData;
        let data = JSON.parse(fileUploadData.csvData);
        deletedColumn.splice(0, 1);
        let col = Object.assign({}, data.columns);
        deletedColumn.forEach((v)=>{
            delete col[v];
        });
        let setData = {...chartData, columns: col};
        let tableData = {...this.state.tableData, chartData: setData, deletedColumn};
        this.setState({ tableData });
    }

    render() {
        const {step,error,tableData,loading,evelResults,predicted,windowWidth, useExternal,interactionState,showSelectTable,internalColumns, showVirtualTable = false} = this.state;
        const {selectedLabel, deletedColumn, chartData, bookmarked} = tableData;
        const isTableView = step < 3 && !error && !!chartData &&
            !loading && !evelResults && !showSelectTable && internalColumns && showVirtualTable;
        let evelTime = new Date(null);
        if(!!evelResults){
           evelTime.setSeconds(evelResults.evalResults.modelResults[evelResults.evalResults.algorithm].Time);
            evelTime = evelTime.toISOString();
            evelTime = evelTime.substr(11, 8);
        }
       return (
          <div className="container">
              <Stepper
                  step={step}
                  onClickStep={this._onClickStep.bind(this)}
                  backOnTableView={this._backOnTableView.bind(this)}
                  backOnResultView={this._backOnResultView.bind(this)}
                  backOnPredictedView={this._backOnPredictedView.bind(this)}
                  forwardOnTrainedView={this.forwardOnTrainedView.bind(this)}
                  backOnUploadView={this._backOnUploadView.bind(this)}
                  stepperData={this.state}
                  onSelectStep = {this._onSelectStep.bind(this)}
                  selectedLabel= {this.state.selectedLabel}
              />
              {interactionState === 1 ?
                  <Container step={step}>
                      <FileUploader
                          ref={(ref) => this.fileUploader = ref}
                          onDrop={this._fileUpload.bind(this)}
                      />

                      {
                          step === 1 ? <ContentStepper
                              responseData={this.state.responseData}
                              ref={(ref) => this.contentStepper = ref}
                              onStart={this._onTrainingStart.bind(this)}
                              onCancel={this._onTrainingCancelReq.bind(this)}
                          /> : null
                      }

                      {
                          step === 1 && !!evelResults && !error ?
                              <div>
                                  {/*<div style={{width:'6%',float:'left',textAlign:'end'}}>*/}
                                  {/*<button className={'back-btn-stepper'} onClick={this._backOnTableView.bind(this)} />*/}
                                  {/*</div>*/}
                                  <div style={{width: '100%', float: 'left'}}>
                                      <FileUploader
                                          isDropzone={true}
                                          onDrop={this._uploadTestFile.bind(this)}
                                      />
                                  </div>
                                  <div>
                                      <table>
                                          <tbody>
                                          <tr>
                                              <td style={{borderBottom: '1px solid #e0e0e0'}}>
                                                  <BarChartComponent
                                                      data={evelResults.evalResults.evalGraphResults['eval']}
                                                      width={(windowWidth / 2) - 20}
                                                  />
                                              </td>
                                              <td style={{
                                                  borderLeft: '1px solid #e0e0e0',
                                                  verticalAlign: 'initial',
                                                  borderBottom: '1px solid #e0e0e0'
                                              }}>
                                                  <table
                                                      style={{width: (windowWidth / 2 - 20) + 'px', marginTop: '10px'}}
                                                      className={'model-result-table'}>
                                                      <tbody>
                                                      <tr>
                                                          <td>Model</td>
                                                          <td>{evelResults.evalResults.algorithm}</td>
                                                      </tr>
                                                      <tr>
                                                          <td>Status</td>
                                                          <td>{'Complete'}</td>
                                                      </tr>
                                                      <tr>
                                                          <td>Time</td>
                                                          <td>{ evelTime }</td>
                                                      </tr>
                                                      <tr>
                                                          <td>Score</td>
                                                          <td>{evelResults.evalResults.modelResults[evelResults.evalResults.algorithm].Score}</td>
                                                      </tr>
                                                      </tbody>
                                                  </table>
                                              </td>
                                          </tr>
                                          </tbody>
                                      </table>
                                  </div>
                              </div>
                              : null
                      }

                      {
                          step === 2 && !!evelResults && !loading && !error && predicted === false ?
                              <div className="stepper-horizontal-container">
                                      {/*<div style={{width:'6%',float:'left',textAlign:'end'}}>*/}
                                      {/*<button className={'back-btn-stepper'} onClick={this._backOnTableView.bind(this)} />*/}
                                      {/*</div>*/}
                                      <div>

                                          <FileUploader
                                              isDropzone={true}
                                              onDrop={this._uploadTestFile.bind(this)}
                                          />
                                      </div>

                                  <p style={{color:'#757575',marginTop: "84px",marginLeft: "-34px", width: "calc(100vw - 100px)", fontFamily: "Roboto, sans-serif"}}>
                                      Vizualize prediction results</p>
                              </div> : null
                      }

                      {
                          (step === 2 && loading) || (loading && !useExternal && step === 0) ?
                              <div className="stepper-horizontal-container">
                                  <img alt="Loading..."
                                       src={`${process.env.PUBLIC_URL}/extensions/MWEx/build/loader.svg`} width={80}/>
                              </div> : null
                      }

                      {
                          step === 2 && !!evelResults && predicted ? <div className="stepper-horizontal-container">
                              <div>
                                  {/*<div style={{width:'6%',float:'left',textAlign:'end'}}>*/}
                                  {/*<button className={'back-btn-stepper'} onClick={this._backOnTableView.bind(this)} />*/}
                                  {/*</div>*/}
                                  {/*<div style={{width:'92%',float:'left'}}>*/}
                                  {/*<FileUploader*/}
                                  {/*isDropzone={true}*/}
                                  {/*onDrop={this._uploadTestFile.bind(this)}*/}
                                  {/*/>*/}
                                  {/*</div>*/}
                              </div>
                              <p style={{color: '#757575', marginTop: "84px", fontFamily: "Roboto, sans-serif"}}>Predict
                                  complete. <a href={window.mwExtVariable.predict_file} >Click here to download</a></p>
                          </div> : null
                      }

                          <VirtualizedTable
                              isShow={isTableView}
                              bookmarked={bookmarked}
                              deletedColumn={deletedColumn}
                              onUndo={this._onUndo.bind(this)}
                              selectedLabels={selectedLabel}
                              useExternal={useExternal}
                              onBookmarkedChart={this.bookmarkedChart.bind(this)}
                              data={chartData}
                              displayColumns={parseInt((windowWidth - 50) / 150)}
                              width={windowWidth - 50}
                              onRemoveColumn={this._onRemoveColumn.bind(this)}
                              onSelectedLabel={this._onSelectedLabel.bind(this)}
                          />

                         <ErrorContainer
                          onOKClick={this._onErrorClick.bind(this)}
                          error={error}
                      />

                      { showSelectTable && !useExternal && interactionState === 1 && !loading  ?
                              <SelectLabelTable
                                  onSelectLabel={this._onSelectLabel.bind(this)}
                              ></SelectLabelTable>
                          : null
                      }

                  </Container>
              :
               null
              }
          </div>
        );
    }
}

export default App;
