import React, { Component } from 'react';
import * as d3 from 'd3';

const generateRandomId = () => {
    let text = "";
    const possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    for (let i = 0; i < 30; i++)
        text += possible.charAt(Math.floor(Math.random() * possible.length));
    return text;
};

class BarChartComponent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            gradRandomId : generateRandomId(),
            margin : { top: 10, right: 10, bottom: 10, left: 150 },
        }
    }

    renderGraph(nextProps) {
        d3.select(this.barChart).selectAll('svg').remove();
        let data = nextProps.data;
        if(!!data && data.length > 0){
            const { margin, gradRandomId } = this.state;
            margin.left = (nextProps.width / 3);
            let width = nextProps.width - margin.left - margin.right,
                height = 400 - margin.top - margin.bottom;

            data = data.sort(function (a, b) {
                return d3.ascending(a.Importance, b.Importance);
            });

            let svgFirst = d3.select(this.barChart).append("svg")
                .attr("width", width + margin.left + margin.right)
                .attr("height", height + margin.top + margin.bottom)
                .attr('fill', '#757575');

            let legend = svgFirst.append("g")
                .attr("transform", "translate(" + (nextProps.width - 100) + "," + 10 + ")");

            legend.append('rect')
                .attr('width', '10')
                .attr('height', '10')
                .attr('x', '-20')
                .attr('y', '-8')
                .attr('fill', "url(#gradient-bar-chart-"+gradRandomId+")");

            legend.append('text')
                .text('Key Drivers')
                .style('font-size','10px');

            let svg = svgFirst.append("g")
                .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

            let x = d3.scale.linear()
                .range([0, width])
                .domain([0, d3.max(data,function (d) { return d.Importance; })])
                .nice();

            let y = d3.scale.ordinal()
                .rangeRoundBands([height, 0], .1)
                .domain(data.map(function (d) {return d.feature;}));

            let yAxis = d3.svg.axis()
                .scale(y)
                .tickSize(0)
                .orient("left");

            svg.append("g")
                .attr("class", "y axis")
                .attr("transform", "translate(" + -25 + "," + 0 + ")")
                .call(yAxis);

            let bars = svg.selectAll(".bar")
                .data(data)
                .enter()
                .append("g");

            bars.append("rect")
                .attr("class", "bar")
                .attr("y", function (d) { return y(d.feature); })
                .attr("height", y.rangeBand())
                .attr("x", 0)
                .attr("width", function (d) { return x(d.Importance); })
                .attr('fill', "url(#gradient-bar-chart-"+gradRandomId+")");

            bars.append("text")
                .attr("class", "label")
                .style('display','none')
                .attr('font-size', '10px')
                .attr("y", function (d) { return y(d.feature) + y.rangeBand() / 2 + 4; })
                .attr("x", function (d) { return x(d.Importance) + 3; })
                .text(function (d) { return d.Importance; });

            let defs = svg.append("defs")
                .append("svg:linearGradient")
                .attr("id", "gradient-bar-chart-"+gradRandomId)
                .attr("x1", "0%")
                .attr("y1", "100%")
                .attr("x2", "100%")
                .attr("y2", "100%")
                .attr("spreadMethod", "pad");

            defs
                .append("stop")
                .attr("offset", "0%")
                .attr("stop-color", '#F28454')
                .attr("stop-opacity", 1);

            defs
                .append("stop")
                .attr("offset", "100%")
                .attr("stop-color", '#EE686A')
                .attr("stop-opacity", 1);
        }
    }

    componentWillReceiveProps(nextProps) {
        if(nextProps !== this.props){
            this.renderGraph(nextProps)
        }
    }

    componentDidMount(){
        this.renderGraph(this.props)
    }

    componentWillUnmount() {
        d3.select(this.barChart).select('svg').remove();
    }

    render() {
        return (<div ref={ (node) => { this.barChart = node } } />);
    }
}

export default BarChartComponent;
