import { Component } from "react";
import React from "react";
import './common.css';

class ErrorContainer extends Component {

    _onClick = () => {
        this.props.onOKClick && this.props.onOKClick();
    }

    render() {
        if(!this.props.error){
            return (null);
        }
        return (
            <div>
                <div className="error-container">
                    <img alt="Error" src={`${process.env.PUBLIC_URL}/extensions/MWEx/build/error.png`} width={50} />
                    <p>
                        {!!this.props.error.heading && (this.props.error.heading !== '' || this.props.error.heading !== null) ? this.props.error.heading : null }
                    </p>
                    <p>
                        {this.props.error.message}
                    </p>
                    <button onClick={this._onClick.bind(this)} className="btn-error "> OK </button>
                </div>
            </div>
        );
    }
}

export default ErrorContainer;
