import React, { Component } from 'react';
import * as d3 from 'd3';

const generateRandomId = () => {
    let text = "";
    const possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    for (let i = 0; i < 30; i++)
        text += possible.charAt(Math.floor(Math.random() * possible.length));
    return text;
};

class BoxPlotTable extends Component {
    constructor(props) {
        super(props);
        this.state = {
            gradRandomId : generateRandomId(),
            margin : { top: 10, right: 10, bottom: -100, left: 10 },
            padding : 20
        }
    }

    renderGraph(props) {
        let data = !!props.matrix_data ? props.matrix_data : null;
        let midline = 50;
        if(!!data && data.length > 0){
            d3.select(this.boxPlot).select('svg').remove();
            const {margin, padding, gradRandomId}  = this.state;
            let width = props.width - margin.left - margin.right;
            let height = props.height - margin.top - margin.bottom;
            midline = (height - padding) / 2;

            let xScale = d3.scale.linear().range([padding, width - padding]);
            let xAxis = d3.svg.axis().scale(xScale).orient('bottom');

            let lowerWhisker = Infinity, q1Val = Infinity, medianVal = 0,
                iqr = 0, upperWhisker = -Infinity, maxVal = -Infinity;

            if(data.length > 0 && data[0]){
                // minVal = data[0].minimum;
                // q3Val = data[0].upper_quartile;
                q1Val = data[0].lower_quartile;
                medianVal = data[0].median;
                lowerWhisker = data[0].lower_whisker;
                upperWhisker = data[0].upper_whisker;
                iqr = data[0].iqr;
                maxVal = data[0].maximum;
            }
            xScale.domain([0, maxVal * 1.1]);

            let svg = d3
                .select(this.boxPlot)
                .append('svg')
                .attr('width', width)
                .attr('height', height)

            svg
                .append('g')
                .attr('class', 'axis')
                .attr('transform', 'translate(0, ' + (height - padding - 95) + ')')
                .call(xAxis);

            svg
                .append("text")
                .attr("transform", "translate(" + (width / 2) + " ," + (height + margin.top + 20 - 41) + ")")
                .style("text-anchor", "middle")
                .style("font-size", "medium")
                .attr('font-family', "Roboto")
                .attr('stroke', "#979797")
                .attr('font-weight', "300")
                .text(props.columnName);

            svg
                .append('line')
                .attr('x1', xScale(lowerWhisker))
                .attr('x2', xScale(lowerWhisker))
                .attr('y1', midline - 15)
                .attr('y2', midline + 15)

            svg
                .append('line')
                .attr('x1', xScale(upperWhisker))
                .attr('x2', xScale(upperWhisker))
                .attr('y1', midline - 15)
                .attr('y2', midline + 15);

            svg
                .append('line')
                .style("stroke-dasharray", ("3, 3"))
                .attr('x1', xScale(lowerWhisker))
                .attr('x2', xScale(upperWhisker))
                .attr('y1', midline)
                .attr('y2', midline);

            svg
                .selectAll('path')
                .style("display","block")
                .style("shape-rendering", "unset")
                .attr("fill","#979797");

            let gradient = svg.append("defs")
                .append("linearGradient")
                .attr("id", "gradient-box-plot"+gradRandomId)
                .attr("x1", "0%")
                .attr("y1", "0%")
                .attr("x2", "100%")
                .attr("y2", "100%")
                .attr("spreadMethod", "pad");

            gradient.append("stop")
                .attr("offset", "0%")
                .attr("stop-color", "#F49646")
                .attr("stop-opacity", 1);

            gradient.append("stop")
                .attr("offset", "100%")
                .attr("stop-color", "#EE656D")
                .attr("stop-opacity", 1);

            svg
                .append('rect')
                .style("fill", "url(#gradient-box-plot"+gradRandomId+")")
                .style("stroke", "#F49646")
                .attr('x', xScale(q1Val))
                .attr('y', padding + 95)
                .attr('width', xScale(iqr) - padding)
                .attr('height', 30);

            svg
                .append('line')
                .attr('x1', xScale(medianVal))
                .attr('x2', xScale(medianVal))
                .attr('y1', midline - 15)
                .attr('y2', midline + 15);


            svg
                .append('g')
                .selectAll('circle')
                .data(data[0].upper_fliers)
                .enter()
                .append('circle')
                .attr('class', 'selected')
                .attr('r', function (d) {
                    if (d < lowerWhisker || d > upperWhisker) return 4;
                    else return 2;
                })
                .attr('cy', function (d) {
                    return random_jitter();
                })
                .attr('cx', function (d) {
                    return xScale(d);
                });
        }

        function random_jitter() {
            let seed;
            if (Math.round(Math.random() * 1) === 0) seed = -5;
            else seed = 5;
            return midline + Math.floor(Math.random() * seed + 1);
        }
    }

    componentWillReceiveProps(nextProps) {
        if(nextProps !== this.props){
            this.renderGraph(nextProps)
        }
    }

    componentDidMount(){
        this.renderGraph(this.props)
    }

    componentWillUnmount() {
        d3.select(this.boxPlot).select('svg').remove();
    }

    render() {
        return (<div ref={ (node) => { this.boxPlot = node } } />);
    }
}

export default BoxPlotTable;
